$primary: #00DCFF;
$primayDark: #0396ad;

a {
  color: $primary;

  &:hover, &:focus {
    color: $primayDark;
  }
}

footer p {
  font-size: 20px;
}

.btn-default {
  color: $primary;
  border: 1px solid $primary;
  background-color: transparent;
  &:focus,
  &:hover {
    border: 1px solid $primary;
    background-color: $primary;
  }
}

img.img-v-logo {
  width: 50%;
}

